import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { RxCommandFactoryService } from '@helix/platform/shared/api';
import { Observable } from 'rxjs';
import { ISummarizationRecord } from './summarization-testing.types';

@Injectable({
  providedIn: 'root'
})
export class RxSummarizationTestingService {
  private textSummarizationCommand = 'com.bmc.arsys.rx.application.cognitive.command.SummarizeTextCommand';
  private recordSummarizationCommand = 'com.bmc.arsys.rx.application.cognitive.command.SummarizeFromRecInstCommand';

  constructor(private httpClient: HttpClient, private rxCommandFactoryService: RxCommandFactoryService) {}

  getTextSummarization(payload: ISummarizationRecord): Observable<string[]> {
    return this.rxCommandFactoryService.forResourceType(this.textSummarizationCommand).execute(payload);
  }

  getRecordInstanceSummarization(payload): Observable<void> {
    return this.rxCommandFactoryService.forResourceType(this.recordSummarizationCommand).execute(payload);
  }
}
